.dashboard-redbloack-contain{
    padding: 30px;
}

.dashboard-asset-items-wrapper{
    margin-bottom: 30px;

}

.assets-item-contain{
    display: flex;
    align-items: center;
    background: white;
    border-radius: 15px;
    font-size: 20px;
    font-weight: bold;
    padding: 10px 20px;
    box-shadow: 6px 4px 25px #ececec;
    height: 100%;
    transition: transform .15s ease-in-out,box-shadow .15s ease-in-out,-webkit-transform .15s ease-in-out;
}

.assets-item-contain:hover{
    webkit-transform: translateY(-.3333333333rem);
    transform: translateY(-2px);
}

.assets-item-contain p{
    overflow: hidden;
}

.assets-item-contain:hover{
    cursor: pointer;
}

.assets-item-img-body{
    min-width: 80px;
    max-width: 80px;
    height: 80px;
    border-radius: 50px;
    background: #4FBF9F1A;
    padding: 25px;
    display: flex;
    align-items: center;
    margin-right: 20px;
}

.green-color{
    color: #4FBF9F;
}

.green-color-round{
    background: #4FBF9F1A;
    border: 1px solid #4FBF9F;
}

.red-color{
    color: #FC573B;
}

.light-red-color{
    color: #FFABAB;
}

.red-color-round{
    background: #FFE6E2;
    border: 1px solid #FC573B;
}

.sky-color{
    color: #17ACE8;
}

.sky-color-round{
    background: #EAF5FB;
    border: 1px solid #17ACE8;
}
.purple-color{
    color: #475F77;
}

.purple-color-round{
    background: #EAF5FB;
    border: 1px solid #475F77;
}

.chart-wrapper .chartjs-tooltip{
    display: none !important;
}

.dashboard-card{
    background: white;
    border-radius: 15px;
    box-shadow: 6px 4px 25px #ececec;
}

.statistics-chart-contain{
    padding: 20px 40px;
}

.asset-sub-title{
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 0px;
}

select.form-control {
    width: 100px;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    background-position: right center;
    background-repeat: no-repeat;
    background-size: 1ex;
    background-origin: content-box;
    background-image: url("data:image/svg+xml;base64,");
}

.flex-vertical-align{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.circle-progress-bar-main{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.circle-progress-bar-contain{
    width: 40%;
    margin: 0 auto;
}

.light-dark-color{
    color: #5E5E5E;
}

.circle-progress-description{
    display: flex;
    justify-content: space-around;
}

.circle-progress-description .flex-vertical-align{
    width: 33.33%;
    align-items: center;
}

.circle-progress-description .flex-vertical-align h5{
    margin-bottom: 0;
}

.circle-progress-description .left-right-both-border{
    border-right: 1px solid #00000029;
    border-left: 1px solid #00000029;
}


/* tab status style */
.tab-status-container{
    padding: 35px 0;
}

.dashboard-img-avator{
    width: 60px;
    border-radius: 30px;
    height: auto;
}

.avatar-comment-item{
    display: flex;
    padding: 15px 20px;
    border-bottom: 1px solid #0000000D;
}

.dashboard-tab-content{
    border-top-left-radius: 0 !important;
}


.dashboard-tab-content .comment-name-address{
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-flow: wrap;
    align-self: center;
    padding: 0 20px;
    flex-direction: column;
}

.dashboard-tab-content .comment-name-address .comment-name{
    color: #354B5E;
    font-weight: bold;
    font-size: 16px;
    margin-bottom: 0;
}

.dashboard-tab-content .comment-name-address .comment-address{
    color: #5E5E5E;
    margin-bottom: 0;
    font-size: 12px;
}

.comment-description-created{
    display: flex;
    flex-direction: column;
    align-self: center;
}

.comment-description-created p{
    margin-bottom: 0;
}

.fade-down{
    animation: fade-down 1s ease-in-out;
}
@keyframes fade-down{
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}

.fade-up{
    animation: fade-up 1s ease-in-out;
}
@keyframes fade-up{
    from{
        opacity: 1;
    }
    to{
        opacity: 0;
    }
}

.avatar-comment-item .comment-created{
    font-size: 12px;
    color: #FFABAB;
}


.avatar-comment-item .comment-description{
    color: #475F77;
    text-align: justify;
}

.dashboard-status-contain .asset-sub-title{
    padding: 17px;
}

.dashboard-tab-content .view-all-btn-contain{
    justify-content: flex-end;
}

.dashboard-tab-content .view-all-btn-contain button{
    border: none;
    outline: none !important;
    background: white;
    color: #D74B4B;
    font-weight: bold;
    padding-top: 20px;
}

.comment-status-main{
    display: flex;
    justify-content: space-between;
    background: white;
    border-radius: 15px;
    padding: 10px 20px;
    margin-bottom: 2px;
    align-items: center;
}

.comment-status-main p{
    margin-bottom: 0;
    padding: 3px 0;
}

.moreVert-icon-btn{
    outline: none !important;
}

.moreVert-icon{
    color: #475F77 !important;
}

.comment-status-date{
    font-size: 12px;
    color: #5E5E5E;
}

.comment-status-time{
    font-size: 14px;
    font-weight: bold;
    color: #354B5E;
}

.chart-keyword-title{
    font-size: 12px;
}

.chart-month-select{
    align-items: flex-end;
}

.chart-wrapper{
    margin-top: 40px;
    height: 300px;
}

.chartjs-render-monitor{
    width: 100% !important;
}

.avatar-name-address-contain{
    display: flex;
    flex: 0 0 30%;
    justify-content: space-around;
}
@media (max-width: 1200px){

    .assets-item-img-body{
        min-width: 50px;
        max-width: 50px;
        height: 50px;
        padding: 10px;
    }

    .assets-item-contain{
        font-size: 16px;
        padding: 15px 20px;
    }

    .chart-wrapper{
        height: 200px;
    }

    .asset-tab-nav{
        padding: 5px 25px 5px;
    }

    .chart-title-description{
        margin-bottom: 10px;
    }

}

@media(max-width: 992px){
    .dashboard-asset-items-wrapper>div{
        margin-bottom: 20px;
    }

    .circle-progress-bar-main{
        margin-top: 30px;
        height: 350px;
    }

    .circle-progress-bar-contain{
        width: 200px;
    }
}

@media (max-width: 768px){
    .avatar-name-address-contain{
        flex: 0 0 40%;
    }
}

@media(max-width: 575px){
    .dashboard-redbloack-contain{
        padding: 15px;
    }

    .statistics-chart-contain{
        padding: 20px;
    }

    .keyword-title-wrapper{
        flex: 0 0 60%;
        max-width: 60%;
    }

    .chart-month-select{
        flex: 0 0 40%;
        max-width: 40%;
    }

    .avatar-comment-item{
        flex-direction: column;
    }

    .avatar-name-address-contain{
        justify-content: space-between;
        margin-bottom: 10px;
    }
}